<template>
  <div class="content">
    <div
      class="hobbies_box"
      v-for="(i,index) in list"
      :key="index"
      :style="`animation-delay:${index*0.2}s;`"
    >
      <div class="l_iocn">
        <i :class="'iconfont ' + i.icon" v-if="i.icon"></i>
        <span class="title" v-else>{{i.title}}</span>
      </div>
      <div class="r_content">
        <p>{{i.text}}</p>
      </div>
    </div>
    <text-bottom :block="false"></text-bottom>
  </div>
</template>

<script>
import TextBottom from "../common/TextBottom";
export default {
  components: {
    "text-bottom": TextBottom
  },
  data() {
    return {
      list: [
        {
          title: "简",
          text: "简书，是我2019年正式加入的。起初是听好友的介绍，说简书的界面简洁，编辑文章也很方便。对于我这种，脑子不太好使的人，是个很方便的工具。我这人比较喜欢记录东西，很多不理解的都会写下来。慢慢的简书也就成为了我成长道路上不可或缺的伙伴。目前也算有200多个小粉丝儿了，自己也会在今后的日子里持续更新。简书名：誰在花里胡哨，首页的个人信息里有详细地址呢~"
        },
        {
          icon: "icongithub",
          text:
            "GitHub应该是在工作时慢慢接触的，主要用于多分支开发，利于团队协作开发。平时我也会针对一些生疏的插件或者 anime.js swiper.js等的使用上，都会有对应的项目记录提交，算是一个储存代码的好地方"
        },
        {
          icon: "iconyinle",
          text: "平时在写代码的时候会听一点歌曲，去听其中的旋律进而提神醒脑。不过我比较喜欢 English 歌曲，😄因为听不懂，但是感觉旋律很好。"
        },
        {
          icon: "icondianying",
          text: "爱看电影这个爱好，算是在学校的时候养成的吧。因为时间比较充裕，个人比较喜欢科幻片，所以现在脑子有很多花里胡哨的想法~~"
        },
        {
          icon: "iconSkiing",
          text:
            "要说滑雪，其实也就在2018年的时候滑了一次。之所以喜欢，是因为第一次我就很快学会了，感觉应该算是我的本命运动之一吧！"
        },
        {
          icon: "iconzuqiu-copy",
          text:
            "小时候就很爱踢足球，两棵树就能算成球门。至今如果能接触到足球，我也是那个跑的最勤快的。"
        },
        {
          icon: "iconpingpang",
          text:
            "乒乓球也是从小就玩的，是我爸经常带着我打，所以小时候接触的较多。技术么~~  不算太好，也不算太坏"
        },
        {
          icon: "iconyumaoqiu",
          text:
            "羽毛球是高中的时候开始接触的，那时候晚上不吃饭就在打，现在很多闲暇时候也会和同事在外面打打"
        },
        {
          icon: "iconyouxi",
          text:
            "打游戏应该算是每个男生都有的爱好吧，平时喜欢王者荣耀，刺激战场，技术么~~ 王者 王牌，游戏技术是拿得出去的。电脑游戏现在很少玩了。"
        }
      ]
    };
  }
};
</script>

<style lang="scss" scoped>
.content {
  padding: 1rem;
}
.hobbies_box {
  margin-top: 1rem;
  padding: 0.5rem;
  background: white;
  transition: 0.3s;
  border: 2px solid transparent;
  box-shadow: 0px 0px 3px #ccc;
  display: flex;
  animation: move 0.5s ease both 1;
  &:hover {
    border: 2px solid var(--base);
    box-shadow: 2px 2px 3px #ccc inset;
  }
  .l_iocn {
    min-width: 3rem;
    //   background: red;
    display: flex;
    justify-content: center;
    align-items: center;
    i {
      font-size: 2rem;
      color: var(--base);
    }
    .title {
      font-size: 1.5rem;
      color: var(--base);
    }
  }
  .r_content {
    padding-left: 1rem;
    font-size: 0.75rem;
    color: #5a5a5a;
  }
}
@keyframes move {
  0% {
    opacity: 0;
    transform: translateY(2rem);
  }
  100% {
    opacity: 1;
    transform: translateY(0rem);
  }
}
</style>


